textarea {
  width: 90%;
  box-sizing: border-box;
  border: solid 2px #1e90ff;
  border-radius: 5px;
  font-size: 16px;
  resize: none;
  padding: 10px;
  vertical-align: middle;
  margin: 5px;
}

.container {
  border: 1px solid gray;
  margin: 5px;
  padding: 1em;
  border-color: black;
  box-shadow: 3px 3px;
  border-radius: 5px;
  background-color: yellow;
}

button {
  width: 7%;
  height: 60px;
  border: 1px solid skyblue;
  background-color: coral;
  padding: 5px;
  border-radius: 5px;
}

#titleBox {
  border: 1px solid gray;
  margin: 5px;
  padding: 1em;
  border-color: black;
  box-shadow: 3px 3px;
  border-radius: 5px;
  background-color: aquamarine;
}

.writer {
  font-weight: bold;
}

#Debate_Room{
    width: 1320px;
    height: 1250px;
}

#writing-area{
    border: 1px solid black;
    background-color: gray;
    border-radius: 5px;
    position: fixed; 
    bottom: 10%;
    left: 15%;
    width: 70%;
    text-align: center;
}

#noneReply{
    text-align: center;
}
